<script>

import { AssinaturaService } from '../../../services'
const assinaturaService = new AssinaturaService();
import moment from 'moment';
export default {
    data() {
        return {
            assinatura: null,
            pacote: null,
            debounce: null,
            carregando: false,
            pagamentos: [],

            sucessoModal: null,
            erroModal: null,

            itemModal: null,

            sucessoLogo: null,
            erroLogo: null,
            logo:null
        };
    },
    mounted() {
        this.buscarStatusAssinatura();
        this.buscarPagamentos();
    },
    methods: {        

        abrirModalDetalhes: function(pgto){
            this.itemModal = pgto;
        },

        buscarStatusAssinatura: function(){
            this.assinatura = this.$store.state.status_assinatura;            
            if(this.assinatura == null){
                this.carregando = true;
                setTimeout(() => {
                    this.buscarStatusAssinatura();
                }, 1000);
            }else{
                this.pacote = this.assinatura.pacote;
                this.carregando = false;
                this.$forceUpdate();
            }
        },

        buscarPagamentos: function(){
            assinaturaService.listarPagamentos({}).then(response => this.pagamentos = response.data.data);
        },

        gerarBoleto: function(){
            this.sucessoModal = null;
            this.erroModal = null;
            this.carregando = true;
            let data = this.data_gerar_boleto(this.pacote.data_proximo_vencimento);
            assinaturaService.gerarBoleto({data_vencimento : data}).then( response => {
                this.sucessoModal = "Boleto Gerado com sucesso!";
                this.assinatura.pagamentosPendentes.push(response.data);
            }).catch( error => this.erroModal = error)
            .finally( () => this.carregando = false)
        },


        data_gerar_boleto: function(data){
            data = moment(data);
            let vencido = moment().add(3, 'days').diff(data) > 0;
            if(vencido)
                return moment().add(3, 'days');
            return data;
        },

        setar_logo: function(event){
            event.preventDefault();
            this.logo = event.target.files[0]
            console.log(this.logo);
        },

        upload_logo: function(){
            this.sucessoLogo = null;
            this.erroLogo = null;

            let formData = new FormData();
            formData.append('arquivo', this.logo);
            assinaturaService.uploadLogo( formData).
                then(response => {
                    this.sucessoLogo = "Logo Atualizada com Sucesso";
                    this.assinatura.logo_impressao = response.data.logo_impressao
                })
                .catch(error => {
                    this.erroLogo = error;
                })
        }
    },

    computed:{
        periodo_avaliacao(){
            if(!this.pacote)
                return false;

            this.pacote.data_limite_teste = moment(this.pacote.data_limite_teste);
            return moment().diff(this.pacote.data_limite_teste) <= 0;
            
        },
        status_assinatura(){
            if(!this.pacote)
                return '';

             let vencimento = moment(this.pacote.data_proximo_vencimento);
             if(moment().diff(vencimento) <= 0)
                return "Vigente Até " + vencimento.format('DD/MM/YYYY');
             
            if(moment().diff(vencimento) <= this.pacote.dias_tolerancia)
                return "Pagamento em Atraso";

            return "Assinatura Suspensa em " + vencimento.add(this.pacote.dias_tolerancia, 'days').format('DD/MM/YYYY');
            
        },
        fidelidade(){
            if(!this.pacote)
                return false;
            let vencimento = moment(this.pacote.fidelidade_vencimento);
             if(moment().diff(vencimento) <= 0)
                return true;
            return false;

        },

    }
};
</script>
